import Data from "../core/data";

const Forms_URL = "https://api.${region}.500apps.com/forms/";
class Forms extends Data {
  constructor() {
    super(Forms_URL);
  }
  /**
   * Preload data
   * @param {Number} form_id - Form ID
   * @param {String} email - Emails
   * @returns - Object
   */
  async getData(form_id, email) {
    this.generateURL(form_id, email);
    let result = await this.get();
    return result;
  }
  /**
   * It saves form data
   * @param {Object} data - Payload
   * @param {Number} form_id - Form ID
   * @param {String} email - Form type email as string
   * @param {String} embed - Form type embed as string
   * @param {Boolean} request_id - Unique for bpm app
   * @returns - Object
   */
  async saveForm(data, form_id, email, embed, request_id) {
    this.generateURL(form_id, email, embed, request_id);
    let result = await this.multi_call(data);
    return result;
  }
  generateURL(form_id, email, embed, request_id) {
    this.disablePCors();
    this.url = "";
    // eslint-disable-next-line no-unused-vars
    let region = this.getRegion();
    this.params = request_id ? `email=${email}&request_id=${request_id}` : embed ? `type=${embed}` : `email=${email}`;
    let url = `${Forms_URL}${window.domain}/${form_id}`;
    this.url = eval("`" + url + "`");
  }
}
export default new Forms();
